import React from 'react'
import Helmet from 'react-helmet';
import Img from 'gatsby-image';

import styles from '../styles/pages/links.module.css';


const Links = ({ links }) => (
  <ul className={styles.links}>
    { 
      links.sort((a, b) => (a.text.localeCompare(b.text))).map(link => (
        <Link key={link.to} to={link.to}>{link.text}</Link>
      )) 
    }  
  </ul>
)

const Link = ({ to, children }) => (
  <li>
    <h3>
      <a target="_blank" href={'http://' + to} > {children} </a>
    </h3>
  </li>
)

const IndexPage = ({ data, windowHeight, windowWidth }) => (
  <div>
    <Img
      style={{
        zIndex: -1,
        position: "fixed",
        right: 0,
        bottom: 0,
        width: windowWidth,
        height: windowHeight,
      }}
      sizes={data.backgroundImage.sizes}
    />
    <Helmet
      title="Berner Teigwaren-Manufaktur | Ingredienza"
      meta={[
        { name: 'description', content: 'Ingredienza – Ihre Teigwaren-Manufaktur aus Bern. Holen Sie sich frische Pastasorten aus der Region. Wir beliefern Ihren Gastronomiebetrieb mit frischen Teigwaren. Einzigartige Kreationen, die jeden Gourmet überraschen. Vielfalt und Kreativität ist bei uns Programm ► Entdecken Sie unser Angebot' },
        { name: 'keywords', content: 'Ingredienza, Freunde und Partner' }

      ]}
    />
    <h1>Freunde und Bekannte</h1>
    <Links links={[
      { to:"www.kursaal-bern.ch", text:"Kursaal Bern" },
      { to:"www.rosengarten.be", text:"Rosengarten Bern" },
      { to:"www.chezvrony.ch", text:"Chez Vrony Zermatt" },
      { to:"www.baeren-oberbottigen.ch", text:"Bären Oberbottigen" },
      { to:"www.eventmakers.ch", text:"Eventmakers Bern" },
      { to:"www.balancehotels.ch/", text:"Seerose Meisterschwanden" },
      { to:"www.wasserngrat.ch", text:"Wasserngrat Saanen" },
      { to:"www.dapino-frohheim.ch", text:"Da Pino Murten" },
      { to:"www.stadthausag.ch/rotonde", text:"Rotonde Biel" },
      { to:"www.goldeneskreuz.ch", text:"Goldenes Kreuz Frauenfeld" },
      { to:"www.urspruenglech.ch", text:"Ur.Sprünglech Sirup Bern" },
      { to:"www.samses.ch", text:"Samses Zürich" },
      { to:"www.eigerbern.ch", text:"Eiger Bern" },
      { to:"www.du-nord.ch", text:"Du Nord Bern" },
      { to:"lebeizli.ch", text:"Le Beizli Köniz" },
      { to:"kirchenfeld.ch", text:"Kirchenfeld Bern" },
      { to:"restaurant-baerengasse.ch", text:"Bärengasse Zürich" },
      { to:"www.noa-restaurant.ch", text:"NOA" },
      { to:"www.restaurant-lechef.ch ", text:"Le Chef " },
    ]} />  
    <h1>Lieferanten und Partner</h1>
    <Links links={[
      { to:"www.fideco.ch", text:"Fideco" }, 
      { to:"www.hosberg.ch", text:"Hosberg" }, 
      { to:"www.biopartner.ch", text:"Bio Partner" }, 
      { to:"www.comestibles-schroeder.ch", text:"Schröder" }, 
      { to:"www.mezzaluna.ch", text:"MezzaLuna Zürich" }, 
      { to:"www.horai.ch", text:"Horai" },
      { to:"www.michel-comestibles.ch", text:"Michel Comestibles Interlaken" },
      { to:"catchmorefish.ch", text:"Gertsch Catch more fish" },
      { to:"www.alaska-wildlachs.ch", text:"Alaska Wildlachs Sam Gugger" },
      { to:"www.polarag.ch", text:"Polar Rheinach" },
    ]} />  
    <h1>Standorte</h1>
    <Links links={[
      { to:"www.petitcouteau.ch", text:"Petit Couteau" },
      { to:"www.aengelibeck.ch", text:"Ängelibeck Köniz" },
    ]} />  

  </div>
)
export const query = graphql`
  query LinksImageQuery {
    backgroundImage: imageSharp(id: { regex: "/linksBg/" }) {
      sizes(maxWidth: 2300) {
        ...GatsbyImageSharpSizes
      }
    }
  }
`;


export default IndexPage



